.header {
  color: #fff;
  padding: 10px;
  overflow: hidden;
}

.name {
  float: left;
  display: inline-block;
  padding: 20px 20px;
  font-size: 14px;
}

.name a {
  color: #fff;
  text-decoration: none;
  text-align: center;
}

.name a h1 {
  font-size: 28px;
  margin-bottom: -4px;
}

.name a h2 {
  font-size: 16px;
}

nav {
  float: right;
  display: inline-block;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  float: right;
  list-style: none;
}

nav a:hover {
  border-bottom: 3px solid #4b6cc1;
}

nav a:active {
  border-bottom: 3px solid #4b6cc1;
}


li a {
  float: left;
  display: block;
  text-align: center;
  color: #fff;
  padding: 5px;
  margin: 30px 20px 0px;
  text-decoration: none;
  font-size: 17px;
  border-bottom: 2px solid transparent;
}
